html {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-text-size-adjust: 100%;
    -ms-text-size-adjust:     100%;
    font-family: sans-serif;
}

*,
*:before,
*:after {
	-webkit-box-sizing: inherit;
	        box-sizing: inherit;
}

ul {
    padding-left: 0;
}

label, input {
    display: block;
}

label {
    font-size: 30px;
    font-weight: bold;
    padding-bottom: 10px;
}

input {
    padding: 10px;
    width: 60%;
    min-width: 320px;
    font-size: 16px;
    border: 2px solid black;
    margin-bottom: 10px;
}

.search-button {
    padding: 10px;
    width: 15%;
    min-width: 300px;
    font-size: 18px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background: firebrick;
    color: white;
    border-radius: 4px;
    border: firebrick;
    font-weight: bold;
}

.search-button:hover {
    background: darkred;
    border: darkred;
}

.loading {
    font-size: 40px;
    font-weight: bold;
}

.map-product-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 20px 15px;
}

.map {
    width: 68%;
}

.product-list-container {
    width: 30%;
}

.product-list-container {
    width: 30%;
    padding-right: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 75vh;
    overflow-y: scroll;
}

.product-item {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 10px 0;
    border-bottom: 3px solid black;
}

.product-item-image {
    width: 25%;
}

.product-item-info {
    width: 75%;
}

.product-item img {
    width: 85%;
    margin: 0 auto;
}

.product-item-name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

.product-item-package {
    margin-top: 0;
}

.product-item-button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 4px;
    background: lightgrey;
    border: lightgrey;
    font-weight: bold;
    margin-bottom: 10px
}

.product-item-button:hover {
    background: grey;
    border: grey;
}

.map-container {
    width: 70%;
    position: relative;
    height: 75vh
}

.google-map {
    width: 50%;
}

.info-window-name {
    font-size: 20px;
    margin-bottom: 5px;
}

.info-window-address {
    margin-top: 5px;
    margin-bottom: 5px;
}

.info-window-city {
    margin-top: 5px;
}

.info-window-quantity {
    font-weight: bold;
    font-size: 16px;
    color: black;   
}

.info-window-quantity span {
    font-weight: bold;
    color: firebrick;
}


/* M E D I A    Q U E R  I E S */
@media (max-width: 1400px) {
    .product-list-container {
        width: 40%;
    }

    .map-container {
        width: 60%;
    }
}

@media (max-width: 1200px) {
    .product-item-image {
        width: 40%;
    }

    .product-item-info {
        width: 60%;
    }
}

@media (max-width: 1000px) {
    .product-list-container,
    .map-container {
        width: 50%;
    }

    .product-item-image {
        width: 30%;
    }

    .product-item-info {
        width: 70%;
    }
}

@media (max-width: 875px) {
    .product-item-image {
        width: 35%;
    }

    .product-item-info {
        width: 65%;
    }
}

@media (max-width: 755px) {
    .product-list-container {
        width: 35%;
    }

    .map-container {
        width: 65%;
    }

    .product-item {
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }

    .product-item-image,
    .product-item-info {
        width: 100%;
        text-align: center;
    }

    .product-item-image img {
        width: 35%;
        margin: 0 auto;
        display: block;
    }
}

@media (max-width: 600px) {
    .map-product-wrapper {
        padding: 20px;
    }
    .product-list-container,
    .map-container {
        width: 100%;
    }

    .product-list-container {
        height: 25vh;
    }

    .product-item-image {
        width: 30%;
    }

    .product-item-info {
        width: 70%;
        text-align: left;
    }

    .product-item-image img {
        width: 70%;
    }
}

@media (max-width: 500px) {
    .product-item-image img {
        width: 100%;
    }
}


